import React from "react";
import styles from "./Council.module.css";
import common from "../../../Style/CommonStyles.module.css";
import Pagination from "../../../Components/DetailComponents/Pagination";
import DetailedSearch from "../../../Components/DetailComponents/DetailedSearch";

const Council = () => {
  return (
    <div className={styles.container}>
      <div className={common.containerWrap}>
        <div>
          <h2> </h2>
          <DetailedSearch />
          <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
            <li></li>
            <li></li>
            <li></li>
            <li> </li>
            <li>()</li>
            <li></li>
          </ul>
          <ul className={`${common.graphList} ${styles.graphWidth}`}>
            <li></li>
            <li></li>
            <li></li>
            <li></li>
            <li>5</li>
            <li> </li>
          </ul>
        </div>
        <Pagination />
      </div>
    </div>
  );
};

export default Council;
