import React from "react";
import PhotoNewsData from "../../Libs/Datas/Main/PhotoNews.json";
import VideoData from "../../Libs/Datas/Main/Video.json";
import InfoData from "../../Libs/Datas/Main/Infos.json";
import ReferenceDate from "../../Libs/Datas/Main/Reference.json";
import Banner from "../../Components/Banner/Banner";
import Header from "../../Components/Header/Header";
import Footer from "../../Components/Footer/Footer";
import styles from "./Main.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight, faCircleChevronRight } from "@fortawesome/free-solid-svg-icons";
import ReactPlayer from "react-player";
import { Link } from "react-router-dom";

const Main = () => {
  return (
    <>
      <Header isDetail={false} />
      <Banner />
      <ContentLayout title={""}>
        <div className={styles.sectionPhotoNews}>
          {PhotoNewsData.map((e) => (
            <Link to="/news">
              <div key={e.id} className={styles.card}>
                <img src={e.image} />
                <div className={styles.contentWrap}>
                  <span className={styles.contentTitle}>{e.title}</span>
                  <span className={styles.content}>{e.description}</span>
                  <div className={styles.additional}>
                    <span className={styles.date}>{e.date}</span>
                    <FontAwesomeIcon icon={faArrowRight} className={styles.arrow} />
                  </div>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </ContentLayout>

      <div className={styles.sectionInfo}>
        <img src="/Images/main/infos/infoLeft.jpg" />
        <img src="/Images/main/infos/infoRight.jpg" />
        <div className={styles.contentWrap}>
          <div className={styles.content}>
            <div className={styles.left}>
              <div className={styles.titleWrap}>
                <img src="/Images/main/infos/infoIcon1.png" />
                <span></span>
              </div>
              <span className={styles.middle}>      .</span>
              <span className={styles.detail}>
                 
                <FontAwesomeIcon icon={faCircleChevronRight} className={styles.arrow} />
              </span>
            </div>

            <div className={styles.right}>
              {InfoData.map((e) => (
                <div key={e.id} className={styles.element}>
                  <img src={e.icon} />
                  <span className={styles.title}>{e.title}</span>
                  <div className={styles.divider} />
                  {e.des.map((el, index) => (
                    <span className={styles.des} key={index}>
                      {el}
                    </span>
                  ))}
                  <div className={styles.more}>+</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      <ContentLayout title={" "}>
        <div className={styles.sectionVideo}>
          {VideoData.map((e) => (
            <div key={e.id} className={styles.playerWrapper}>
              <ReactPlayer url={e.url} className={styles.reactPlayer} width="100%" height="100%" />
            </div>
          ))}
        </div>
        <div className={`${styles.sectionVideo} ${styles.titleWrap}`}>
          {VideoData.map((e) => (
            <span className={styles.videoTitle} key={e.id}>
              {e.title}
            </span>
          ))}
        </div>
      </ContentLayout>

      <div className={styles.sectionReference}>
        <div className={styles.wrap}>
          <span className={styles.title}></span>
          {ReferenceDate.map((e) => (
            <img key={e.id} src={e.image} onClick={() => window.open(e.link, "_blank")}></img>
          ))}
        </div>
      </div>

      <Footer />
    </>
  );
};

interface ContentLayout {
  title: string;
  children: React.ReactNode;
}

const ContentLayout = ({ title, children }: ContentLayout) => {
  return (
    <div className={styles.contentLayout}>
      <span className={styles.title}>{title}</span>
      <div className={styles.children}>{children}</div>
    </div>
  );
};

export default Main;
