import React from "react";
import styles from "./Equipment.module.css";
import { useNavigate } from "react-router-dom";
import common from "../../../Style/CommonStyles.module.css";
import Pagination from "../../../Components/DetailComponents/Pagination";
const Equipment = () => {
  const navigate = useNavigate();

  return (
    <div className={styles.container}>
      <div className={common.searchContainer}>
        <div className={common.searchWrap}>
          <select>
            <option value="default"></option>
          </select>
          <input placeholder=" ." />
          <img className={common.searchIcon} src="Icon/magnifyingGlass.png" alt="search" />
        </div>
        <select className={common.sort}>
          <option value="default"></option>
        </select>
      </div>
      <div className={styles.EquipmentWrap} onClick={() => navigate("/equipmentDetail")}>
        {/**code repeat1 */}
        <div className={styles.eachEquipment}>
          <img className={styles.equipmentPic} src="/Images/tempEquipment.png" alt="equipment Pic" />
          <div className={styles.equipmentInfo}>
            <p className={styles.bold}> </p>
            <br />
            <p className={styles.bold}>1 3 </p>
            <br />
            <br />
            <p>(055-249-2206)</p>
          </div>
        </div>
        {/**code repeat2 */}
        <div className={styles.eachEquipment}>
          <img className={styles.equipmentPic} src="/Images/tempEquipment.png" alt="equipment Pic" />
          <div className={styles.equipmentInfo}>
            <p className={styles.bold}> </p>
            <br />
            <p className={styles.bold}>1 3 </p>
            <br />
            <br />
            <p>(055-249-2206)</p>
          </div>
        </div>

        {/**code repeat3 */}
        <div className={styles.eachEquipment}>
          <img className={styles.equipmentPic} src="/Images/tempEquipment.png" alt="equipment Pic" />
          <div className={styles.equipmentInfo}>
            <p className={styles.bold}> </p>
            <br />
            <p className={styles.bold}>1 3 </p>
            <br />
            <br />
            <p>(055-249-2206)</p>
          </div>
        </div>

        {/**code repeat2 */}
        <div className={styles.eachEquipment}>
          <img className={styles.equipmentPic} src="/Images/tempEquipment.png" alt="equipment Pic" />
          <div className={styles.equipmentInfo}>
            <p className={styles.bold}> </p>
            <br />
            <p className={styles.bold}>1 3 </p>
            <br />
            <br />
            <p>(055-249-2206)</p>
          </div>
        </div>
      </div>
      <Pagination />
    </div>
  );
};

export default Equipment;
