import { BrowserRouter, Route, Routes } from "react-router-dom";
import Main from "./Pages/MainPage/Main";
import * as React from "react";
import Login from "./Pages/LoginPage/Login";
import DetailPage from "./Pages/DetailPage/Detail";
import MenuList from "./Libs/Datas/MenuList.json";
import EquipmentDetail from "./Pages/InfraPages/EquipmentDetailPage/EquipmentDetail";
import StudentsDetailPage from "./Pages/ServicePages/StudentsDetailPage/StudentsDetailPage";
import UniversityInfoDetailPage from "./Pages/SuppoetPages/UniversityInfoDetailPage/UniversityInfoDetailPage";
import MileageDetailPage from "./Pages/SuppoetPages/MileageDetailPage/MileageDetailPage";
import Join from "./Pages/JoinPage/Join";
import News from "./Components/News/News";
function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/login" element={<Login />} />
        <Route path="/join" element={<Join />} />
        <Route path="/news" element={<News />} />
        {/* <Route path="/equipmentDetail" element={<EquipmentDetail />} />
        <Route path="/studentsDetailPage" element={<StudentsDetailPage />} />
        <Route
          path="/universityInfoDetailPage"
          element={<UniversityInfoDetailPage />}
        />
        <Route path="/mileageDetailPage" element={<MileageDetailPage />} /> */}
        {MenuList.map((e, ind) =>
          e.childs.map((el, index) => (
            <Route
              key={el.link}
              path={el.link}
              element={<DetailPage parent={ind} child={index} />}
            />
          ))
        )}
      </Routes>
    </BrowserRouter>
  );
}

export default App;
