import { useEffect, useState } from "react";
import Header from "../../Components/Header/Header";
import InfoBanner from "../../Components/InfoBanner/InfoBanner";
import MenuList from "../../Libs/Datas/MenuList.json";
import DetailM from "./Detail.m";
import styles from "./Detail.module.css";
import Footer from "../../Components/Footer/Footer";

const IMAGES = [
  "Images/InfoPages.jpg",
  "Images/InfraPages.jpg",
  "Images/EmploymentPages.jpg",
  "Images/SuppoetPages.jpg",
  "Images/DevelopmentPages.jpg",
  "Images/MouPages.jpg",
  "Images/ServicePages.jpg",
];

interface Props {
  parent: number;
  child: number;
}
const DetailPage = ({ parent, child }: Props) => {
  const [nowParent, setNowParent] = useState(MenuList[parent]);
  const [nowChild, setNowChild] = useState(MenuList[parent].childs[child]);
  const Page = DetailM[parent] ? DetailM[parent][child] : "";

  useEffect(() => {
    setNowParent(MenuList[parent]);
    setNowChild(MenuList[parent].childs[child]);
  }, [parent]);

  useEffect(() => {
    setNowChild(MenuList[parent].childs[child]);
  }, [child]);

  return (
    <div style={{ width: "100%", display: "flex", flexDirection: "column" }}>
      <Header isDetail={true} />
      <InfoBanner
        title={nowChild.bannerTitle}
        subTitle={nowChild.bannerSubTitle}
        backgroundImage={IMAGES[parent]}
        parent={nowParent.parent}
        child={nowChild.title}
        name={nowChild.title}
        header={nowChild.header}
      />
      <div className={styles.DetailChild}>{Page}</div>
      <Footer />
    </div>
  );
};

export default DetailPage;
