import React, { useEffect, useRef, useState } from "react";
import styles from "./Header.module.css";
import MenuList from "../../Libs/Datas/MenuList.json";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUnlockKeyhole,
  faMagnifyingGlass,
} from "@fortawesome/free-solid-svg-icons";

interface Props {
  ref: React.MutableRefObject<HTMLInputElement | null>;
  callback(): void;
}

const Header = ({ isDetail }: { isDetail: boolean }) => {
  const [hovered, setHovered] = useState(isDetail ? true : false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [searchIconClicked, setSearchIconClicked] = useState(false);
  const headerRef = useRef(null);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
    if (searchIconClicked) setSearchIconClicked(false);
  };

  const handleSearchClick = () => {
    setSearchIconClicked(!searchIconClicked);
    if (menuOpen) setMenuOpen(false);
  };

  const menuLinks = MenuList;

  const useClickOutside = ({ ref, callback }: Props) => {
    useEffect(() => {
      const handleClickOutside = (event: MouseEvent) => {
        if (ref.current && !ref.current.contains(event.target as Node)) {
          callback();
        }
      };

      document.addEventListener("click", handleClickOutside);
      return () => {
        document.removeEventListener("click", handleClickOutside);
      };
    }, [ref, callback]);
  };

  useClickOutside({ ref: headerRef, callback: () => setMenuOpen(false) });
  useClickOutside({
    ref: headerRef,
    callback: () => setSearchIconClicked(false),
  });

  return (
    <header ref={headerRef}>
      <div className={`${styles.headerContainer} ${styles.headerContainerhover}`}>
        {/* <div
        className={`${styles.headerContainer} ${hovered || menuOpen || searchIconClicked ? styles.headerContainerhover : ""}`}
        onMouseEnter={() => !isDetail && setHovered(true)}
        onMouseLeave={() => !isDetail && setHovered(false)}
      > */}
        <div className={styles.menuIcon} onClick={toggleMenu}>
          <img src={menuOpen ? "Icon/allMenu02.png" : "Icon/allMenu01.png"} alt="" />
        </div>

        <div className={styles.headerInner}>
          <Link to="/">
            <h1>
              <img src="Icon/lincLogo.png" alt="" />
              {/* <img src={hovered || menuOpen || searchIconClicked ? "Icon/lincLogo.png" : "Icon/lincWhiteLogo.png"} alt="" /> */}
            </h1>
          </Link>

          <ul className={styles.menu}>
            {menuLinks.map((element, index) => {
              return (
                <li key={index} className={styles.mainMenu}>
                  <Link to={element.childs[0].link} className={styles.menuLinkHover}>
                    {element.parent}
                  </Link>

                  {/* <Link to={element.childs[0].link} className={hovered || menuOpen || searchIconClicked ? styles.menuLinkHover : ""}>
                    {element.parent}
                  </Link> */}
                  <div>
                    <nav>
                      <ul className={styles.subMain}>
                        {element.childs.map((subMenuItem, index) =>
                          subMenuItem.header === false ? (
                            ""
                          ) : (
                            <li
                              key={index}
                              id={subMenuItem.title}
                              className={`${styles.subMainList} ${
                                searchIconClicked || menuOpen
                                  ? styles.hideSubMenu
                                  : ""
                              }`}
                            >
                              <Link to={subMenuItem.link}>
                                {subMenuItem.title}
                              </Link>
                            </li>
                          )
                        )}
                      </ul>
                    </nav>
                  </div>
                </li>
              );
            })}
          </ul>

          <div className={styles.headerRight}>
            <img src={searchIconClicked ? "Icon/search02.png" : "Icon/search.png"} alt="" onClick={handleSearchClick} />
            <Link to="/login">
              <div className={styles.loginBtn}>
                <FontAwesomeIcon icon={faUnlockKeyhole} />
                <span></span>
              </div>
            </Link>
          </div>
        </div>

        <div className={`${styles.allMenu} ${menuOpen ? styles.allMenuShow : ""}`}>
          <ul className={styles.allMenuInner}>
            {menuLinks.map((e, index) => (
              <li key={index} className={styles.allMainMenu}>
                <Link to="/">{e.parent}</Link>
                <div>
                  <nav>
                    <ul className={styles.allSubMain}>
                      {e.childs.map((subMenuItem, index) => (
                        <li key={index} id={subMenuItem.title} className={styles.allMSubMainList}>
                          <Link to={subMenuItem.link}>{subMenuItem.title}</Link>
                        </li>
                      ))}
                    </ul>
                  </nav>
                </div>
              </li>
            ))}
          </ul>
        </div>

        <div className={`${styles.searchBox} ${searchIconClicked ? styles.searchBoxShow : ""}`}>
          <div className={styles.search}>
            <label></label>
            <input type="text" placeholder="  ." className={styles.searchBtn} />
            <FontAwesomeIcon icon={faMagnifyingGlass} className={styles.searchIcon} />
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
