import React from "react";
import styles from "./Greeting.module.css";

const Greeting = () => {
  return (
    <div className={styles.container}>
      <div className={styles.greetingWrap}>
        <img className={styles.schoolImg02} src="Images/responsive/greeting01.jpg" alt="school" />
        <div className={styles.textWrap}>
          <p className={styles.title}>
              <br />
              .
          </p>
          <br />
          <p className={styles.content}>
                    2004   .  
                            2012 
                .
            <br />
            <br />
                      ,      ,
                   .
            <br />
            <br />
                ,          .
            <br />
            <br />
              /             
                             
             .
            <br />
            <br />
                /          
                            .
            <br />
            <br />
            .
            <br />
            <br />
            <br />
            <p className={styles.name}>  </p>
          </p>
        </div>
        <img className={styles.schoolImg} src="Images/GreetingPic.png" alt="school" />
      </div>
      <img className={styles.sign} src="Icon/sign.png" alt="signature" />
    </div>
  );
};

export default Greeting;
