import React from "react";
import css from "./InfoBanner.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown } from "@fortawesome/free-solid-svg-icons";

interface Props {
  backgroundImage: string;
  title: string;
  subTitle: string;
  parent: string;
  child: string;
  name: string;
  header: boolean | undefined;
}

const InfoBanner = (props: Props) => {
  return (
    <div className={css.container}>
      <div className={css.imageWrap}>
        <img className={css.infoPic} src={props.backgroundImage} alt="info banner" />
        <div className={css.textWrap}>
          <h1 className={css.title}>{props.title}</h1>
          <p className={css.subTitle}>{props.subTitle}</p>
        </div>
        <div className={css.dropDownWrap}>
          <div className={css.selectWrap}>
            <div className={css.IconWrap}>
              <img className={css.homeIcon} src="Icon/homeIcon.png" alt="point" />
              <img className={css.nextIcon} src="Icon/next.png" alt="next" />
              <select className={css.mainCategory}>
                <option value="default">{props.parent}</option>
              </select>
              <FontAwesomeIcon icon={faCaretDown} className={css.caretDown} />
              <img className={css.nextIcon} src="Icon/next.png" alt="next" />
              <select className={css.subCategory}>
                <option value="default">{props.child}</option>
              </select>
              <FontAwesomeIcon icon={faCaretDown} className={css.caretDown} />
            </div>
          </div>
          <div className={css.titleWrap}>
            <div className={css.subtitleWrap}>
              <img className={css.iconPoint} src="Icon/point.png" alt="point" />
              <p className={css.infoTitle}>{props.name}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default InfoBanner;
