import React, { useState } from "react";
import styles from "./Footer.module.css";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";

const Footer = () => {
  const [familyContentVisible, setFamilyContentVisible] = useState(false);

  const toggleFamilyContent = () => {
    setFamilyContentVisible(!familyContentVisible);
  };

  return (
    <footer>
      <div className={styles.footerTop}>
        <div className={styles.menuMove}>
          <Link to="/greeting">
            <p></p>
          </Link>
          <Link to="/university-info">
            <p> </p>
          </Link>
          <Link to="/technology-transfer">
            <p> </p>
          </Link>
          <Link to="/project">
            <p> </p>
          </Link>
          <Link to="/">
            <p></p>
          </Link>
        </div>
        <div className={styles.familySite}>
          <button className={styles.familyBtn} onClick={toggleFamilyContent}>
            Family Site
            <FontAwesomeIcon icon={faPlus} className={styles.faPlus} />
          </button>
          <ul className={`${styles.familyContent} ${familyContentVisible ? styles.familyContentShow : ""}`}>
            <li>
              <a href="http://www.kubi.or.kr/"> </a>
            </li>
            <li>
              <a href="http://www.kubi.or.kr/"> </a>
            </li>
            <li>
              <a href="http://www.kubi.or.kr/"> </a>
            </li>
          </ul>
        </div>
      </div>

      <div className={styles.footerBottom}>
        <div>
          <div className={styles.address}>
            <img src="Icon/lincAllWhite.png" alt="" />
            <p>(51767)     7()  </p>
            <p className={styles.copyright}>
              Copyright  Industry-Academic Cooperation Foundation of Kyungnam University. All Rights Reserved.
            </p>
          </div>
          {/* <div className={styles.callCenter}>
            <p> T 055-249-2103 F 0505-999-2127</p>
            <p> T 055-249-2456 F 0505-999-2127</p>
            <p> T 055-249-2239 F 0505-986-2182</p>
            <p> T 055-249-2298 F 0505-999-2127</p>
          </div> */}
          <table className={styles.callCenter}>
            <tbody>
              {[
                ["", "T", "055-249-2103", "F", "0505-999-2127"],
                ["", "T", "055-249-2456", "F", "0505-999-2127"],
                ["", "T", "055-249-2239", "F", "0505-986-2182"],
                ["", "T", "055-249-2298", "F", "0505-999-2127"],
              ].map((e, i) => (
                <tr key={i}>
                  {e.map((el, ind) => (
                    <td key={ind}>{el}</td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
