import React, { useState } from "react";
import common from "../../Style/CommonStyles.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretDown, faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";

const DetailedSearch = () => {
  const [showDropdown, setShowDropdown] = useState(false);

  const toggleDropdown = () => {
    setShowDropdown(!showDropdown);
  };

  return (
    <div className={common.searchContainer}>
      <div className={common.searchDrop}>
        <button onClick={toggleDropdown} className={common.searchBtn}>
           <FontAwesomeIcon icon={faCaretDown} className={common.triangle} />
        </button>
        {showDropdown && (
          <div className={common.searchContent}>
            <p></p>
            <p></p>
            <p></p>
          </div>
        )}
      </div>

      <div className={common.searchBox}>
        <input type="text" placeholder="  ." className={common.searchBoxBtn} />
        <FontAwesomeIcon icon={faMagnifyingGlass} className={common.searchBoxIcon} />
      </div>
    </div>
  );
};

export default DetailedSearch;
