import React from "react";
import styles from "./Login.module.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser, faLock } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

const Login = () => {
  return (
    <div className={styles.container}>
      <div className={styles.loginForm}>
        <img src="Icon/linclogo.png" alt="" />
        <h2>LOGIN</h2>
        <div className={styles.inputWrap}>
          <div className={styles.inputForm}>
            <div>
              <FontAwesomeIcon icon={faUser} className={styles.loginIcon} />
              <input type="text" placeholder="" className={styles.loginBox} />
            </div>
            <div>
              <FontAwesomeIcon icon={faLock} className={styles.loginIcon} />
              <input type="paddword" placeholder="" className={styles.loginBox} />
            </div>
          </div>
          <div className={styles.userInfo}>
            <p> </p>
            <p> </p>
            <Link to="/join" className={styles.joinLink}>
              <p></p>
            </Link>
          </div>
          <button className={styles.loginButton}></button>
        </div>
      </div>
    </div>
  );
};

export default Login;
