import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAnglesLeft, faAngleLeft, faAngleRight, faAnglesRight } from "@fortawesome/free-solid-svg-icons";
import common from "../../Style/CommonStyles.module.css";

const Pagination = () => {
  return (
    <div className={common.pagination}>
      <FontAwesomeIcon icon={faAnglesLeft} className={common.paginationIcon} />
      <FontAwesomeIcon icon={faAngleLeft} className={common.paginationIcon} />
      <p>1</p>
      <FontAwesomeIcon icon={faAngleRight} className={common.paginationIcon} />
      <FontAwesomeIcon icon={faAnglesRight} className={common.paginationIcon} />
    </div>
  );
};

export default Pagination;
