import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowUpRightFromSquare } from "@fortawesome/free-solid-svg-icons";
import styles from "./Student.module.css";
import common from "../../../Style/CommonStyles.module.css";
import "./Calendar.css";
import Calendar from "react-calendar";
import { Link } from "react-router-dom";

type ValuePiece = Date | null;

const Student = () => {
  const [calendarValue, setCalendarValue] = useState<ValuePiece | [ValuePiece, ValuePiece]>(new Date());

  return (
    <div className={styles.container}>
      <div className={common.containerWrap}>
        <div>
          <h2>  </h2>
          <ul className={`${[common.graphTitle]} ${styles.graphWidth}`}>
            <li></li>
            <li>()</li>
            <li>()</li>
            <li>3   (A)</li>
            <li> (B)</li>
            <li></li>
            <li> </li>
          </ul>
          <ul className={`${common.graphList} ${styles.graphWidth}`}>
            <li></li>
            <li></li>
            <Link to="/StudentsDetailPage" className={styles.studentlink}>
              <li>
                
                <FontAwesomeIcon icon={faArrowUpRightFromSquare} className={styles.linkIcon} />
              </li>
            </Link>
            <li>0</li>
            <li>267</li>
            <li>47</li>
            <li>0</li>
          </ul>
        </div>

        <div className={styles.contentWrap}>
          <div className={styles.studentContents}>
            <div className={styles.contentList}>
              <div>
                <img src="Icon/student01.png" alt="" />
                <span>Planning</span>
                <h3></h3>
              </div>
            </div>
            <div className={styles.contentList}>
              <div>
                <img src="Icon/student02.png" alt="" />
                <span>Development</span>
                <h3></h3>
              </div>
            </div>
            <div className={styles.contentList}>
              <div>
                <img src="Icon/student03.png" alt="" />
                <span>Employment</span>
                <h3></h3>
              </div>
            </div>
            <div className={styles.contentList}>
              <div>
                <img src="Icon/student04.png" alt="" />
                <span>Counseling</span>
                <h3></h3>
              </div>
            </div>
          </div>
        </div>

        <div className={styles.calendarWrap}>
          <h2> </h2>
          <div className={styles.calendarList}>
            <Calendar onChange={(v, e) => setCalendarValue(v)} value={calendarValue} />
            <div className={styles.announcement}>
              <span></span>
              <p>CJ()</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Student;
