import React from "react";
import styles from "./Join.module.css";

const Join = () => {
  return (
    <div className={styles.container}>
      <div className={styles.joinForm}>
        <img src="Icon/linclogo.png" alt="" />
        <h2></h2>
        <div className={styles.inputWrap}>
          <div className={styles.inputForm}>
            <p></p>
            <input type="text" placeholder=" " className={styles.loginBox} />
            <em></em>
            <p></p>
            <input type="password" placeholder=" 8 " className={styles.loginBox} />
            <p> </p>
            <input type="password" placeholder=" " className={styles.loginBox} />
            <p></p>
            <input type="text" placeholder="  " className={styles.loginBox} />
            <p> </p>
            <input type="text" placeholder="'-' " className={styles.loginBox} />
            <em> </em>
            <p></p>
            <input type="text" placeholder=" " className={styles.loginBox} />
            <p></p>
            <input type="text" placeholder=" 8" className={styles.loginBox} />
          </div>

          <div className={styles.agreement}>
            <div>
              <input type="radio" />
              <p> .</p>
            </div>
            <div>
              <input type="radio" />
              <p> </p>
            </div>
            <div>
              <input type="radio" />
              <p>  </p>
            </div>
            <div>
              <input type="radio" />
              <p>    ()</p>
            </div>
          </div>
          <button className={styles.joinButton}></button>
        </div>
      </div>
    </div>
  );
};

export default Join;
