import React from "react";
import styles from "./InfoNameCard.module.css";

interface Props {
  image: string;
  team: string;
  name: string;
  contact: string;
  position: string;
  email: string;
}
const InfoNameCard = (prop: Props) => {
  return (
    <div className={styles.card}>
      <div className={styles.profile}>
        <img className={styles.profilePic} src={prop.image} alt="profile" />
        <div className={styles.profileInfo}>
          <p className={styles.CardInfoTeam}>{prop.team}</p>
          <div className={styles.subInfoWrap}>
            <div className={styles.upWrap}>
              <p className={styles.title}>
                <strong className={styles.titleMargin}></strong>
                <em className={`${styles.content} ${styles.left}`}>{prop.name}</em>
              </p>

              <p className={styles.title}>
                <strong></strong>
                <em className={styles.content}>{prop.contact}</em>
              </p>
            </div>
            <div className={styles.underWrap}>
              <p className={styles.title}>
                <strong className={styles.titleMargin}></strong>
                <em className={`${styles.content} ${styles.left}`}>{prop.position}</em>
              </p>

              <p className={styles.title}>
                <strong></strong>
                <em className={styles.content}>{prop.email}</em>
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.duty}>
        <li>  </li>
      </div>
    </div>
  );
};
export default InfoNameCard;
